'use strict';

/**
* Cauchy distribution probability density function (PDF).
*
* @module @stdlib/stats-base-dists-cauchy-pdf
*
* @example
* import pdf from '@stdlib/stats-base-dists-cauchy-pdf';
*
* var y = pdf( 2.0, 0.0, 1.0 );
* // returns ~0.063
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-cauchy-pdf';
*
* var pdf = factory( 10.0, 2.0 );
*
* var y = pdf( 10.0 );
* // returns ~0.159
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
